'use client'

import { useState } from 'react'
import useSWR, { mutate } from 'swr'
import Image from 'next/image'
import { Plus, Pencil, Trash2, MoreHorizontal } from 'lucide-react'
import { Button } from '@/components/ui/button'
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '@/components/ui/dialog'
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table'
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuLabel,
    DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import { ServiceForm } from '@/components/admin/ServiceForm'

interface Service {
    id: string
    title: string
    category: string
    price: number
    images: string[]
    description?: string
    duration?: string
    location?: string
    maxPeople?: number
    languages?: string[]
    included?: string[]
    requirements?: string[]
}

const fetcher = async (url: string) => {
    const res = await fetch(url)
    const text = await res.text()
    try {
        return JSON.parse(text)
    } catch (e) {
        console.error(`JSON Parse Error for ${url}:`, text)
        throw e
    }
}

export default function ServicesPage() {
    const { data: services, error, isLoading } = useSWR<Service[]>('/api/services', fetcher)
    const [isDialogOpen, setIsDialogOpen] = useState(false)
    const [editingService, setEditingService] = useState<Service | null>(null)

    const handleCreate = async (data: Omit<Service, 'id'>) => {
        await fetch('/api/services', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data),
        })
        mutate('/api/services')
        setIsDialogOpen(false)
    }

    const handleUpdate = async (data: Partial<Service>) => {
        if (!editingService) return
        await fetch(`/api/services/${editingService.id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data),
        })
        mutate('/api/services')
        setIsDialogOpen(false)
        setEditingService(null)
    }

    const handleDelete = async (id: string) => {
        if (!confirm('Are you sure you want to delete this service?')) return
        await fetch(`/api/services/${id}`, {
            method: 'DELETE',
        })
        mutate('/api/services')
    }

    if (error) return <div>Failed to load services</div>
    if (isLoading) return <div>Loading...</div>

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h1 className="text-3xl font-bold">Services</h1>
                <Dialog open={isDialogOpen} onOpenChange={(open) => {
                    setIsDialogOpen(open)
                    if (!open) setEditingService(null)
                }}>
                    <DialogTrigger asChild>
                        <Button>
                            <Plus className="mr-2 h-4 w-4" />
                            Add Service
                        </Button>
                    </DialogTrigger>
                    <DialogContent className="sm:max-w-[500px]">
                        <DialogHeader>
                            <DialogTitle>{editingService ? 'Edit Service' : 'Add New Service'}</DialogTitle>
                        </DialogHeader>
                        <ServiceForm
                            initialData={editingService}
                            onSubmit={editingService ? handleUpdate : handleCreate}
                            onCancel={() => setIsDialogOpen(false)}
                        />
                    </DialogContent>
                </Dialog>
            </div>

            <div className="rounded-md border">
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>Image</TableHead>
                            <TableHead>Title</TableHead>
                            <TableHead>Category</TableHead>
                            <TableHead>Price</TableHead>
                            <TableHead className="text-right">Actions</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {services?.map((service) => (
                            <TableRow key={service.id}>
                                <TableCell>
                                    {service.images[0] && (
                                        <div className="relative h-10 w-16 rounded overflow-hidden">
                                            <Image
                                                src={service.images[0]}
                                                alt={service.title}
                                                fill
                                                className="object-cover"
                                            />
                                        </div>
                                    )}
                                </TableCell>
                                <TableCell className="font-medium">{service.title}</TableCell>
                                <TableCell>{service.category}</TableCell>
                                <TableCell>${service.price}</TableCell>
                                <TableCell className="text-right">
                                    <DropdownMenu>
                                        <DropdownMenuTrigger asChild>
                                            <Button variant="ghost" className="h-8 w-8 p-0">
                                                <span className="sr-only">Open menu</span>
                                                <MoreHorizontal className="h-4 w-4" />
                                            </Button>
                                        </DropdownMenuTrigger>
                                        <DropdownMenuContent align="end">
                                            <DropdownMenuLabel>Actions</DropdownMenuLabel>
                                            <DropdownMenuItem onClick={() => {
                                                setEditingService(service)
                                                setIsDialogOpen(true)
                                            }}>
                                                <Pencil className="mr-2 h-4 w-4" />
                                                Edit
                                            </DropdownMenuItem>
                                            <DropdownMenuItem onClick={() => handleDelete(service.id)} className="text-red-600">
                                                <Trash2 className="mr-2 h-4 w-4" />
                                                Delete
                                            </DropdownMenuItem>
                                        </DropdownMenuContent>
                                    </DropdownMenu>
                                </TableCell>
                            </TableRow>
                        ))}
                        {services?.length === 0 && (
                            <TableRow>
                                <TableCell colSpan={5} className="h-24 text-center">
                                    No services found.
                                </TableCell>
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </div>
        </div>
    )
}
